body {
	background: rgb(3, 40, 33);
	background: linear-gradient(
		90deg,
		rgba(3, 40, 33, 1) 0%,
		rgba(4, 30, 46, 1) 100%
	);
	color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100vw;
}

.app {
	display: flex;
	align-items: center;
	gap: 50px;
	background-color: rgba(255, 255, 255, 0.2);
	padding: 20px;
	border-radius: 25px;
	border: 2px solid rgba(255, 255, 255, 0.3);
}

.pie-chart {
	width: 500px;
	height: 500px;
}

.user-list {
	height: 480px;
	padding: 5px;
	background-color: rgba(255, 255, 255, 0.2);
	border: 2px solid rgba(255, 255, 255, 0.3);
	border-radius: 10px;
	overflow-y: scroll;
	overflow-x: hidden;
}
.list-card {
	display: grid;
	grid-template-columns: 35px 215px 30px;
	position: relative;
	height: 30px;
}

.list-card .name {
	overflow-x: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.list-card .count {
	text-align: center;
}

.list-card img {
	height: 30px;
}

::-webkit-scrollbar {
	width: 3px;
}

::-webkit-scrollbar-thumb {
	background-color: white;
}

@media screen and (max-width: 900px) {
	.app {
		flex-direction: column;
		height: 600px;
		width: 400px;
	}

	.user-list {
		max-height: 300px;
		width: 300px;
	}

	.pie-chart {
		width: 300px;
		height: 400px;
	}
}

@media screen and (max-width: 500px) {
	.app {
		width: 200px;
		height: 400px;
	}

	.user-list {
		width: 200px;
	}

	.pie-chart {
		height: 200px;
		width: 200px;
	}

	.list-card {
		display: grid;
		grid-template-columns: 35px 115px 30px;
		position: relative;
		height: 30px;
	}
}
